import React, { FC } from "react";
import { DeepMap, FieldError } from "react-hook-form";
import "./form-input.scss";

interface FormInputProps {
  placeholder: string;
  errors: DeepMap<Record<string, any>, FieldError>;
  name: string;
  type: string;
  register: any;
  required: any;
  title?: string;
  customStyle?: any;
  onChange?: any;
  step?: string;
  min?: string;
  max?: string;
}

export const FormInput: FC<FormInputProps> = ({
  placeholder,
  errors,
  name,
  type,
  register,
  title,
  required,
  customStyle,
  onChange,
  ...props
}) => {
  return (
    <div className="Container" style={customStyle}>
      <p className="Row-Title">{title}</p>
      <div className="Container-Row">
        <input
          className={`Container-Row-Input ${
            errors[name] ? "Container-Row-Input-Error" : ""
          }`}
          type={type}
          step="0.01"
          name={name}
          onChange={(e) => (onChange ? onChange(e.target.value) : null)}
          placeholder={placeholder}
          ref={register(required)}
          {...props}
        />
        {type === "checkbox" && (
          <b className="Container-Row-Title">{placeholder}</b>
        )}
      </div>
      <div className={`Error ${errors[name] ? "Error-Trans" : ""}`}>
        {errors[name] && <b className="Error-Text">Value required</b>}
      </div>
    </div>
  );
};
