import React, { FC, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { FormInput } from "../../components/FormInput/FormInput";
import { useHistory } from "react-router-dom";
import { Preloader } from '../../components/Preloader/Preloader'

type LoginForm = {
  email: string;
  password: string;
};

interface LoginProps {
  fetchAuthUser: (email: string, password: string, history: any, onSucess?: () => void, onError?: (err: any) => void) => void;
}

export const Login: FC<LoginProps> = ({ fetchAuthUser }) => {
  let history = useHistory();
  const { register, handleSubmit, errors } = useForm();

  const [error, setError] = useState<any>('')
  const [loading, setLoading] = useState<boolean>(false)

  const onSubmit = (data: LoginForm) => {
    setError('')
    setLoading(true)
    fetchAuthUser(data.email, data.password, history, () => {
      setLoading(false)
    }, (err) => {
      setError(err)
      setLoading(false)
    });
  };

  if (loading) {
    return <Preloader />
  }

  return (
    <>
      <div className="Login-Header">
        <h1 className="Login-Header-Title">Welcome to Bairro Admin</h1>
      </div>
      <div className="Login-Form">
        <h1 className="Login-Form-Title">Login</h1>
        <form
          className="Login-Form-Container"
          onSubmit={handleSubmit(onSubmit)}
        >
          <FormInput
            placeholder={"Email"}
            name={"email"}
            type={"text"}
            errors={errors}
            register={register}
            required={{ required: true }}
          />
          <FormInput
            placeholder={"Password"}
            name={"password"}
            type={"text"}
            errors={errors}
            register={register}
            required={{ required: true }}
          />
          <input type="submit" className="Login-Form-Button" />
        </form>
        <div style={{ marginTop: 15, color: 'red' }} >{error}</div>
      </div>
    </>
  );
};
